import {GameObject} from "./GameObject";
import {Context} from "../Drawing/Context";
import {IClickHandler} from "./IClickHandler";

export class Button extends GameObject{
    public text : string;
    public backgroundColor : string;
    public hoverColor : string;
    public activeColor : string;

    private _wasButtonDown : boolean;
    private _listener : Array<IClickHandler>;


    constructor(x : number, y : number, width: number, height : number){
        super(x,y,width,height);
        this._listener = new Array<IClickHandler>();

        this.backgroundColor = "#ff9900";
        this.hoverColor = "#ddd";
        this.activeColor ="#87cefa";
    }

    public draw(drawContext : Context){
        let color = this.isMouseOver ? this.hoverColor : this.backgroundColor;
        color = this.isMouseDown ? this.activeColor : color;
        drawContext.drawFilledRectangle(this, color);
        drawContext.drawCenteredText(this.middlePoint, this.text,"#000",30);        
        drawContext.drawRectangle(this.x, this.y,this.width,this.height, "#000");
    }

    public addClickListener(listener : IClickHandler){
        this._listener.push(listener);
    }

    public update() : void{
        if(this.isMouseDown && !this._wasButtonDown){
            this._listener.forEach(l => l(this, undefined));
        }
        this._wasButtonDown = this.isMouseDown;
    }
}